	module.exports = {"data":{"markdownRemark":{"html":"<h2>Business Value</h2>\n<p>Business Value is the focus of agile delivery. The agile team is charged with evaluating the business value of all work.</p>\n<p>Business value describes what the customer will get for a specific piece of work.</p>\n<p>This value is discovered through conversations with the customer and analysis of the work involved. Business stakeholders may have data that quantifies the value of a given feature that has been requested. A product owner will use the various sources of information available and devote significant time to understanding, evaluating, and prioritizing the value the customer will receive for a given piece of work. </p>","fields":{"slug":"/agile/business-value/"},"frontmatter":{"title":"Business Value"}}},"pathContext":{"slug":"/agile/business-value/"}}

